import {
    UrpCoreCommand,
    UrpCommand
    // @ts-ignore
} from '@mtrust/urp-types/wrapper_pb';

// this is the same for all commands, thats why its ok to use the SEC one
import {
    UrpSecCommandWrapper,
    // @ts-ignore
} from '@mtrust/urp-types/sec_pb';



import {
    CommandBuilder,
} from './command_builder';

// Usage
const coreCmdBuilder = new CommandBuilder(UrpSecCommandWrapper, UrpCoreCommand);

export const pingMsg = () => coreCmdBuilder.buildMessage(UrpCommand.URPPING);
export const getNameMsg = () => coreCmdBuilder.buildMessage(UrpCommand.URPGETNAME);
export const getInfoMsg = () => coreCmdBuilder.buildMessage(UrpCommand.URPGETINFO);
export const getPublicKeyMsg = () => coreCmdBuilder.buildMessage(UrpCommand.URPGETPUBLICKEY);
export const identifyMsg = () => coreCmdBuilder.buildMessage(UrpCommand.URPIDENTIFY);
export const offMsg = () => coreCmdBuilder.buildMessage(UrpCommand.URPOFF);
export const rebootMsg = () => coreCmdBuilder.buildMessage(UrpCommand.URPREBOOT);