import axios from 'axios';
import * as base64 from 'base-64';
import {
    getPublicKeyMsg,
    getInfoMsg,
    offMsg,
    rebootMsg    // @ts-ignore
} from './core_commands';

export { sdk_version } from './version';

import {
    secGetModelInfoMsg,
    secGetTokenMsg,
    secForwardMsg
    // @ts-ignore
} from './sec_commands';

import {
    impGetTokenMsg,
    impForwardMsg
    // @ts-ignore
} from './imp_commands';

import {
    UrpToken,
    UrpSecureToken
    // @ts-ignore
} from '@mtrust/urp-types/token_pb';

// @ts-ignore
import { BleServiceUUIDs, BleRXCharacteristicUUIDs } from '@mtrust/urp-types/ble_uuids';
// @ts-ignore
import { UrpMessage, UrpDeviceInfo, UrpSecModels, UrpPublicKey } from '@mtrust/urp-types/types';
import { DfuHandler } from './dfu_handler';

// @ts-ignore
export { UrpDeviceType } from '@mtrust/urp-types/types';
// @ts-ignore
export { BleServiceUUIDs } from '@mtrust/urp-types/ble_uuids';

/**
 * Handles communication and device operations over Bluetooth.
 */
export class DeviceHandler {

    public info: UrpDeviceInfo | null;

    private _reconnect: boolean = false;
    private _reconnectCallback: (d: DeviceHandler) => void = (d: DeviceHandler) => {
        console.warn("No reconnect callback set, but device disconnected.");
    };

    private _commCharacteristic: BluetoothRemoteGATTCharacteristic | undefined;
    private _dfuHandler: DfuHandler | null;
    private _connected: boolean;
    private _messageMap: Map<any, any>;
    private _seqNr: number;
    private _device: BluetoothDevice | null;
    private _deviceType: string | null;
    private _updateCallback: (e: Object) => void;
    private _notifyConnect: () => void;
    private _notifyDisconnect: () => void;
    private _models: any[];
    private _dataInBuffer: Uint8Array;
    private _dataInBufferIndex: number;
    private _remainingExpectedBytes: number;
    private _batteryLevel: Number | null;
    private _publicKey: string | null;
    private _token: UrpToken | null;
    private _ready: boolean = false;

    private _cmdQueue: UrpMessage[];
    private _cmdQueueInterval: any | null;

    private _packageUpdateStartCallback: ((e: { fileSize: number, chunkSize: number, totalChunks: number }) => void) | null;
    private _packageUpdateProgressCallback: ((e: { progress: number, totalChunks: number, remainingChunks: number }) => void) | null;
    private _packageUpdateFinishCallback: ((e: { rebootNeeded: boolean, error?: any }) => void) | null;

    private _logCallback: (d: UrpMessage) => void;

    /**
     * Creates an instance of DeviceHandler.
     * @param {number} index - The index of the device.
     * @param {BluetoothDevice| null} device - The Bluetooth device to handle.
     * @param {() => void} connectCallback - Callback function for device connection.
     * @param {() => void} disconnectCallback - Callback function for device disconnection.
     * @param {(e: Object) => void} updateCallback - Callback function for updates.
     */
    constructor(index: number, device: BluetoothDevice| null, connectCallback: () => void, disconnectCallback: () => void, updateCallback: (e: Object) => void) {
        this._commCharacteristic = undefined; // Used for general communication
        this._dfuHandler = null;  // Used for firmware updates only
        this._connected = false;

        this._messageMap = new Map();
        this._seqNr = 0;
        this._deviceType = null;
        this._ready = false;

        this._updateCallback = updateCallback;
        this._notifyConnect = connectCallback;
        this._notifyDisconnect = disconnectCallback;

        this._logCallback = (d: UrpMessage) => { console.warn(d); };

        this._packageUpdateStartCallback = null;
        this._packageUpdateProgressCallback = null;
        this._packageUpdateFinishCallback = null;

        device?.addEventListener('gattserverdisconnected', () => {
            this._connected = false;
            this._notifyDisconnect();

            if(this._reconnect) {
                this._reconnectWithBackoff();
            }
        });

        this._device = device;

        this._publicKey = null;
        this._models = [];
        this.info = null;

        this._remainingExpectedBytes = -1;
        this._dataInBuffer = new Uint8Array();
        this._dataInBufferIndex = 0;

        this._batteryLevel = null;

        if( device ){
            this._initDevice();
        } 

        this._cmdQueue = [];
        this._cmdQueueInterval = null;
    }

    static fromSerialization(device: any) {
        const handler = new DeviceHandler(
            0,
            null,
            () => {},
            () => {},
            () => {}
        );

        // Handle Map serialization properly
        handler._messageMap = new Map(); // Start with empty map, as we can't transfer callbacks
        handler._commCharacteristic = undefined; // Can't transfer WebBluetooth characteristics
        handler._dfuHandler = null; // Will be reinitialized when needed
        handler._connected = false; // Start disconnected
        handler._seqNr = 0; // Reset sequence number
        
        // Copy over the serializable properties
        handler._deviceType = device._deviceType;
        handler.info = device.info;
        handler._dataInBuffer = new Uint8Array(0); // Reset buffer
        handler._dataInBufferIndex = 0;
        handler._remainingExpectedBytes = -1;
        handler._batteryLevel = device._batteryLevel;
        handler._publicKey = device._publicKey;
        handler._models = Array.isArray(device._models) ? [...device._models] : [];
        handler._token = device._token;
        handler._dfuHandler = device._dfuHandler ? DfuHandler.fromSerialization(device._dfuHandler) : null;

        return handler;
    }

    setAutoReconnect = (reconnect: boolean) => {
        this._reconnect = reconnect;
    }

    setReconnectCallback = (callback: (d: DeviceHandler) => void) => {
        this._reconnectCallback = callback;
    }

    _reconnectWithBackoff = async () => {
        return new Promise((resolve) => {
            setTimeout(async () => {
                try {
                    await this._initDevice();
                    if (this._ready) {
                        this._reconnectCallback(this);
                        resolve(this);
                    } else {
                        resolve(await this._reconnectWithBackoff());
                    }
                } catch (error) {
                    console.error("Error during reconnection attempt:", error);
                    resolve(await this._reconnectWithBackoff());
                }
            }, 1000);
        });
    }

    waitForDeviceReady = async (): Promise<void> => {
        if (this._ready) {
            return Promise.resolve();
        }
        
        return new Promise((resolve) => {
            const interval = setInterval(() => {
                if (this._ready) {
                    clearInterval(interval);
                    resolve();
                }
            }, 100);
        });
    }

    /**
     * Initializes the device by connecting to the GATT server and setting up characteristics.
     */
    private async _initDevice() {
        this._cmdQueueInterval = null;
        this._seqNr = 0;
        this._dataInBuffer = new Uint8Array();
        this._dataInBufferIndex = 0;
        this._remainingExpectedBytes = -1;
        this._deviceType = null;
        this._batteryLevel = null;
        this._publicKey = null;
        this._token = null;
        this._models = [];
        this._dfuHandler = null;
        this._messageMap.clear();
        this._commCharacteristic = undefined;
        this.info = null;
        this._ready = false;
        try {
            // Connect to the GATT server of the selected device
            const server = await this._device?.gatt?.connect();

            // Get all services
            const services = await server?.getPrimaryServices();
            if (services) {
                for (const service of services) {
                    const deviceType = this._identifyDeviceByServiceUUID(service.uuid);
                    if (deviceType !== null) {
                        this._deviceType = deviceType;
                    }
                }
            }

            // Check if deviceType was found
            if (this._deviceType === null) {
                return;
            }

            // Get the communication service
            const commService = await server?.getPrimaryService(BleServiceUUIDs.get(this._deviceType) as BluetoothServiceUUID);

            // Get the characteristic for communication
            this._commCharacteristic = await commService?.getCharacteristic(BleRXCharacteristicUUIDs.get(this._deviceType) as BluetoothCharacteristicUUID);
            this._commCharacteristic?.addEventListener('characteristicvaluechanged', this._bleReceiveCallback);

            // Enable notifications for the characteristic
            await this._commCharacteristic?.startNotifications();

            // Get the DFU service and characteristic
            this._dfuHandler = null;
            try {
                const dfuService = await server?.getPrimaryService(0xFE59);
                const dfuCharacteristic = await dfuService?.getCharacteristic(0x0002);

                if (dfuCharacteristic) {
                    this._dfuHandler = new DfuHandler(dfuCharacteristic);
                    if (this._packageUpdateStartCallback) {
                        this._dfuHandler.setStartCallback(this._packageUpdateStartCallback);
                    }
                    if (this._packageUpdateProgressCallback) {
                        this._dfuHandler.setUpdateCallback(this._packageUpdateProgressCallback);
                    }
                    if (this._packageUpdateFinishCallback) {
                        this._dfuHandler.setEndCallback(this._packageUpdateFinishCallback);
                    }
                }
            } catch (error: any) {
                console.error("Device does not support dfu service!");
            }


            // Get the battery service and characteristic
            try {
                const batteryService = await server?.getPrimaryService(0x180F);
                if (batteryService) {
                    const batteryCharacteristic = await batteryService?.getCharacteristic(0x2A19);

                    if (batteryCharacteristic) {
                        batteryCharacteristic.addEventListener('characteristicvaluechanged', (e: Event) => {
                            const target = e.target as BluetoothRemoteGATTCharacteristic;
                            const batteryLevel: number = target.value!.getUint8(0);
                            this._batteryLevel = batteryLevel;
                            this._updateCallback({ battery: this._batteryLevel });
                        });
                        await batteryCharacteristic!.startNotifications();
                        await batteryCharacteristic!.readValue();
                    }
                }
            } catch (e: any) {
                console.error("Device does not support battery service!");
            }

            this._connected = true;
            this._notifyConnect();

            this._get_info();
            this._get_public_key();
            this._ready = true;
        } catch (error: any) {
            console.error("Error connecting to device:", error);
        }
    }

    /**
     * Handles the characteristic value change event and processes incoming data.
     * @param {Event} ev - The event containing updated characteristic value.
     */
    private _bleReceiveCallback = (ev: Event) => {
        try {
            const target = ev.currentTarget as BluetoothRemoteGATTCharacteristic;
            const data = target.value as DataView;
            this._dataIn(data);
        } catch (error) {
            console.error('Error handling characteristic value change:', error);
        }
    };

    /**
     * Processes incoming data and extracts complete messages.
     * @param {DataView} d - The DataView containing the incoming data.
     */
    private _dataIn = (d: DataView) => {
        const data = new Uint8Array(d.buffer);

        let messageOffset = 0;
        const dataLength = data.length;

        while (messageOffset < dataLength) {
            if (this._remainingExpectedBytes === -1) {
                if (dataLength - messageOffset < 2) {
                    console.error("Not enough bytes to read length");
                    return;
                }

                const msgLength = (data[this._dataInBufferIndex] << 8) | data[this._dataInBufferIndex + 1];
                this._remainingExpectedBytes = msgLength;

                this._dataInBufferIndex = 0;
                messageOffset += 2;
            }

            const leftInBuffer = dataLength - messageOffset;
            const bytesToRead = Math.min(this._remainingExpectedBytes, leftInBuffer);

            const combinedData = new Uint8Array(this._dataInBuffer.length + bytesToRead);
            combinedData.set(this._dataInBuffer, 0);
            combinedData.set(data.slice(messageOffset, messageOffset + bytesToRead), this._dataInBuffer.length);

            this._dataInBuffer = combinedData;

            messageOffset += bytesToRead;
            this._remainingExpectedBytes -= bytesToRead;
            this._dataInBufferIndex += bytesToRead;

            if (this._remainingExpectedBytes === 0) {
                const msg = UrpMessage.deserializeBinary(this._dataInBuffer);
                if (msg !== null) {
                    this._onResponse(msg);
                }
                this._resetBuffer();
            }
        }
    };

    /**
     * Resets the buffer used for receiving data.
     */
    private _resetBuffer = () => {
        this._remainingExpectedBytes = -1;
        this._dataInBufferIndex = 0;
        this._dataInBuffer = new Uint8Array();
    };

    /**
     * Handles the response message received from the device.
     * @param {UrpMessage} msg - The received message.
     */
    private _onResponse = (msg: UrpMessage) => {
        const obj = msg.toObject();
        const key = obj.header.seqnr;
        const callback = this._messageMap.get(key);
        this._messageMap.delete(key);
        if(callback){
            callback(msg.toObject());
        } else {
            this._logCallback(msg.toObject());
        }
    }

    /**
     * Identifies the device type based on its service UUID.
     * @param {string} uuid - The UUID of the service.
     * @returns {string | null} The identified device type or null if not found.
     */
    private _identifyDeviceByServiceUUID(uuid: string) {
        for (const [key, value] of BleServiceUUIDs.entries()) {
            if (value === uuid) {
                return key;
            }
        }
        return null;
    }

    /**
     * Converts an integer value to a byte array.
     * @param {number} val - The integer value to convert.
     * @returns {number[]} The byte array representing the integer value.
     */
    private _intToBytes = (val: number) => {
        const lsb = val & 0xFF;
        const msb = (val >> 8) & 0xFF;
        return [msb, lsb];
    };

    /**
     * Packs data with its length in bytes.
     * @param {Uint8Array} data - The data to pack.
     * @returns {number[]} The packed data including its length.
     */
    private _pack = (data: Uint8Array) => {
        const bytes = this._intToBytes(data.length);
        return new Uint8Array([...bytes, ...data]);
    };

    private _addCmd = (msg: UrpMessage, callback: Function) => {
        this._messageMap.set(this._seqNr, callback);
        const header = msg.getHeader();
        header.setSeqnr(this._seqNr);
        msg.setHeader(header);
        this._seqNr += 1;

        // push to queue
        console.log("Adding command to queue");
        this._cmdQueue.push(msg);

        // if queue is not running
        if (this._cmdQueueInterval == null) {
            // start the queue
            this._send();
        }
    }

    private _send = async () => {
        console.debug("Sending cmd queue...");
        this._cmdQueueInterval = 1; // to avoid a retriggering

        if (this._cmdQueue && this._cmdQueue.length > 0) {
            var msg: UrpMessage = this._cmdQueue.shift();

            const packedData = new Uint8Array(this._pack(msg.serializeBinary()));
            const CHUNK_SIZE = 512;

            if (this._commCharacteristic) {
                let offset = 0;
                while (offset < packedData.length) {
                    const chunk = packedData.slice(offset, offset + CHUNK_SIZE);
                    try {
                        await this._commCharacteristic.writeValue(chunk);
                    } catch (error) {
                        console.warn("Error writing chunk to characteristic. Might be because the device rebooted or disconnected.");
                        return;
                    }
                    offset += CHUNK_SIZE;
                }
            } else {
                // if we failed to send, we push it back into the queue
                this._cmdQueue.push(msg);
            }
            if (this._cmdQueue.length > 0) {
                this._cmdQueueInterval = setTimeout(() => this._send(), 200);
            } else {
                this._cmdQueueInterval = null;
            }
        }
    }

    /**
     * Sets the callback function to be invoked when a package update starts.
     * @param {(e: { fileSize: number, chunkSize: number, totalChunks: number }) => void} startCallback - Callback function for package update start.
     */
    setPackageUpdateStartCallback = (startCallback: (e: { fileSize: number, chunkSize: number, totalChunks: number }) => void) => {
        this._packageUpdateStartCallback = startCallback;
        if(this._dfuHandler !== null){
            this._dfuHandler.setStartCallback(startCallback);
        } else {
            throw new Error("Setting a callback makes no sense without a DFU characteristic");
        }
    }

    /**
     * Sets the callback function to be invoked during a package update.
     * @param {(e: { progress: number, totalChunks: number, remainingChunks: number }) => void} progressCallback - Callback function for package update progress.
     */
    setPackageUpdateProgressCallback = (progressCallback: (e: { progress: number, totalChunks: number, remainingChunks: number }) => void) => {
        this._packageUpdateProgressCallback = progressCallback;
        if(this._dfuHandler !== null){
            this._dfuHandler.setUpdateCallback(progressCallback);
        } else {
            throw new Error("Setting a callback makes no sense without a DFU characteristic");
        }
    }

    /**
     * Sets the callback function to be invoked when a package update finishes.
     * @param {(e: { rebootNeeded: boolean, error?: any }) => void} finishCallback - Callback function for package update finish.
     */
    setPackageUpdateFinishCallback = (finishCallback: (e: { rebootNeeded: boolean, error?: any }) => void) => {
        this._packageUpdateFinishCallback = finishCallback;
        if(this._dfuHandler !== null){
            this._dfuHandler.setEndCallback(finishCallback);
        } else {
            throw new Error("Setting a callback makes no sense without a DFU characteristic");
        }
    }

    setLogCallback = (logCallback: (d: UrpMessage) => void) => {
        this._logCallback = logCallback;
    }

    /**
     * Disconnects from the Bluetooth device.
     */
    disconnect = () => {
        this._device?.gatt?.disconnect();
    }

    _get_device_specific_info = () => {
        this._get_token();
        if (this.isImp()) {
        }
        if (this.isSec()) {
            this.get_sec_model_info();
        }
    }

    forwardCmd = (serialized_wrapper: Uint8Array, callback: Function) => {
        if(this.isSec()){
            this._addCmd(secForwardMsg(serialized_wrapper), callback);
        } else if (this.isImp()) {
            this._addCmd(impForwardMsg(serialized_wrapper), callback);
        } else {
            console.warn("Forwarding token is only supported for SEC and IMP devices!");
        }
    }

    publicKey = () => {
        return this._publicKey;
    }

    token = () => {
        return this._token;
    }

    dfuHandler = () => {
        return this._dfuHandler;
    }

    /**
     * Requests device information from the device.
     * @returns {Promise<void>} A promise that resolves when the information is received.
     */
    _get_info = () => {
        console.log("Requesting device info...");
        return this._addCmd(getInfoMsg(), (res: any) => {
            const info = UrpDeviceInfo.deserializeBinary(res.response.payload).toObject() as UrpDeviceInfo;

            console.log("Device info received:", info);

            // parse firmware version
            // this should only happen in dev or qc versions
            const ending = info.fwVersion.split("-")[1];
            if(!ending) {
                info.fwType = "release";
            } else if (ending && ending == "D") {
                info.fwType = "development";
            } else if (ending && ending == "Q") {
                info.fwType = "quality";
            } else if (ending && ending == "R") {
                info.fwType = "training";
            } else if (ending) {
                info.fwType = "unknown (" + ending + ")";
            }
            info.fwVersion = info.fwVersion.split("-")[0];

            // assign new version info
            this.info = info;
            this._updateCallback(info);

            // pull more info that is dependent on info
            this._get_device_specific_info();

        });
    }

    _get_token = () => {
        if (this.isImp()) {
            return this._addCmd(impGetTokenMsg(), (res: any) => {
                const token = UrpToken.deserializeBinary(res.response.payload).toObject() as UrpToken;
                this._token = token;
                this._updateCallback(token);
            });
        }
        if (this.isSec()) {
            return this._addCmd(secGetTokenMsg(), (res: any) => {
                const token = UrpToken.deserializeBinary(res.response.payload).toObject() as UrpToken;
                this._token = token;
                this._updateCallback(token);
            });
        }
    }

    off = () => {
        this._addCmd(offMsg(), (res: any) => {});
    }

    reboot = () => {
        this._addCmd(rebootMsg(), (res: any) => {});
    }

    isSec = () => {
        return this.info?.devicetype === 4;
    }
    isImp = () => {
        return this.info?.devicetype === 6;
    }
    isImz = () => {
        return this.info?.devicetype === 7;
    }

    models = () => {
        return this._models;
    }

    batteryLevel = () => {
        return this._batteryLevel;
    }


    /**
     * Reboots the device after a firmware update.
     * @returns {Promise<void>} A promise that resolves when the device reboots.
     */
    rebootAfterUpdate = async () => {
        this._dfuHandler?.rebootAfterUpdate();
    }

    /**
     * Requests the public key from the device.
     * @returns {Promise<void>} A promise that resolves when the public key is received.
     */
    _get_public_key = () => {
        return this._addCmd(getPublicKeyMsg(), (res: any) => {
            const key = UrpPublicKey.deserializeBinary(res.response.payload).toObject() as UrpPublicKey;

            // Decode Base64 to a binary string (ArrayBuffer)
            const decodedPublicKey = atob(key.value);

            // Convert the binary string to a readable string
            let decodedString = '';
            for (let i = 0; i < decodedPublicKey.length; i++) {
                decodedString += String.fromCharCode(decodedPublicKey.charCodeAt(i));
            }
            this._publicKey = decodedString;
            this._updateCallback(decodedString);
        });
    }

    /**
     * Requests model information from the device.
     * @returns {Promise<void>} A promise that resolves when the model information is received.
     */
    get_sec_model_info = () => {
        return this._addCmd(secGetModelInfoMsg(), (res: any) => {
            const info = UrpSecModels.deserializeBinary(res.response.payload).toObject() as UrpSecModels;
            this._models = info.modelsList;
            this._updateCallback(info);
        });
    }

    /**
     * Checks if the device is connected.
     * @returns {boolean} True if the device is connected, otherwise false.
     */
    isConnected = () => {
        return this._connected;
    }

    /**
     * Checks if the device can be updated (i.e., if DFU is supported).
     * @returns {boolean} True if the device can be updated, otherwise false.
     */
    canBeUpdated = () => {
        return this._dfuHandler !== null;
    }

    /**
     * Uploads a firmware file to the device.
     * @param {File} file - The firmware file to upload.
     * @param {boolean} [autoReboot=true] - Indicates whether the device should reboot automatically after the update.
     */
    uploadFirmware = (file: File, autoReboot: boolean = true) => {
        this._dfuHandler?.handleFirmwareUpload(file, autoReboot);
    }

    /**
     * Uploads a model file to the device.
     * @param {File} file - The model file to upload.
     * @param {boolean} [autoReboot=true] - Indicates whether the device should reboot automatically after the update.
     */
    uploadModel = (file: File, autoReboot: boolean = true) => {
        this._dfuHandler?.handleModelUpload(file, autoReboot);
    }

    cancelUpload = () => {
        this._dfuHandler?.cancel();
    }
}
