import {
    UrpImpCommandWrapper,
    UrpImpDeviceCommand,
    UrpImpCommand
    // @ts-ignore
} from '@mtrust/urp-types/imp_pb';

import {
    UrpTokenRequest
    // @ts-ignore
} from "@mtrust/urp-types/token_pb";

import {
    UrpMessage,
    UrpMessageHeader,
    UrpRequest
    // @ts-ignore
} from "@mtrust/urp-types/wrapper_pb";

import {
    CommandBuilder,
} from './command_builder';

// Usage
const impCmdBuilder = new CommandBuilder(UrpImpCommandWrapper, UrpImpDeviceCommand);

export const impGetTokenMsg = () => {
    return impCmdBuilder.buildMessage(UrpImpCommand.URPIMPGETTOKEN);
}

export const impRequestTokenMsg = (amount = 10) => {
    const tokenRequest = new UrpTokenRequest();
    tokenRequest.setAmount(amount);

    const msg = new UrpMessage();
    const header = new UrpMessageHeader();
    msg.setHeader(header);

    const wrapper = new UrpImpCommandWrapper();
    const cmd = new UrpImpDeviceCommand();
    cmd.setCommand(UrpImpCommand.URPIMPREQUESTTOKEN);
    cmd.setTokenrequest(tokenRequest);

    wrapper.setDevicecommand(cmd);

    const request = new UrpRequest();
    request.setPayload(wrapper.serializeBinary());
    msg.setRequest(request);
    return msg;
}

export const impForwardMsg = (serialized_wrapper: Uint8Array) => {
    const msg = new UrpMessage();
    const header = new UrpMessageHeader();
    msg.setHeader(header);
    
    const request = new UrpRequest();
    request.setPayload(serialized_wrapper);
    msg.setRequest(request);
    return msg;
}