import {
    UrpCoreCommand, UrpMessage,
    UrpMessageHeader,
    UrpRequest
    // @ts-ignore
} from '@mtrust/urp-types/wrapper_pb';

type CommandConstructor<T> = new () => T;

type WrapperType = {
    setCorecommand?: (cmd: any) => void;
    setDevicecommand?: (cmd: any) => void;
    serializeBinary: () => Uint8Array;
};

type CommandType = {
    setCommand: (command: any) => void;
    [key: string]: any;
};

export class CommandBuilder<W extends WrapperType, C extends CommandType> {
    private wrapperType: CommandConstructor<W>;
    private commandType: CommandConstructor<C>;

    constructor(wrapperType: CommandConstructor<W>, commandType: CommandConstructor<C>) {
        this.wrapperType = wrapperType;
        this.commandType = commandType;
    }

    buildMessage(command: any, payload: any = null): UrpMessage {
        const msg = new UrpMessage();
        const header = new UrpMessageHeader();
        msg.setHeader(header);

        const wrapper = new this.wrapperType();
        const cmd = new this.commandType();
        cmd.setCommand(command);


        // Handle different wrapper types with specific methods
        if (cmd instanceof UrpCoreCommand && wrapper.setCorecommand) {
            wrapper.setCorecommand(cmd);
        } else if (wrapper.setDevicecommand) {
            wrapper.setDevicecommand(cmd);
        } else {
            console.warn("Could not build command!", cmd)
        }

        const request = new UrpRequest();
        request.setPayload(wrapper.serializeBinary());
        msg.setRequest(request);
        return msg;
    }
}
