import { DeviceHandler, BleServiceUUIDs, UrpDeviceType } from "@mtrust/console-kit";

export class BleConnection {
    private deviceHandler: DeviceHandler | null = null;
    private onDeviceUpdateHandler: ((deviceHandler: DeviceHandler) => void) | null = null;
    private progress: number = 0;
    private connectionStatus: string = "disconnected";

    constructor(
        onDeviceUpdateHandler?: (deviceHandler: DeviceHandler) => void
    ) {
        if (onDeviceUpdateHandler) {
            this.onDeviceUpdateHandler = onDeviceUpdateHandler;
        }
    }

    // Set the device update handler
    public setOnDeviceUpdateHandler(handler: (deviceHandler: DeviceHandler) => void): void {
        this.onDeviceUpdateHandler = handler;
    }

    // Get connection status
    public getConnectionStatus(): string {
        return this.connectionStatus;
    }

    // Get current progress
    public getProgress(): number {
        return this.progress;
    }

    // Upload package to the connected device
    public uploadPackage(data: { variant: number; data: Uint8Array; reboot: boolean }): void {
        console.log("Uploading package:", data);
        if (data.variant === 1 && this.deviceHandler) {
            this.deviceHandler.uploadFirmware(
                new File([data.data], "firmware", { type: "application/octet-stream" }),
                data.reboot
            );
        } else {
            console.warn("Device handler is not set or invalid package data.");
        }
    }

    // Connect to a Bluetooth device using the Web Bluetooth API
    public async connectBleDevice(serviceUUIDs: UrpDeviceType[]): Promise<DeviceHandler | null> {
        try {
            const device = await navigator.bluetooth.requestDevice({
                filters: serviceUUIDs.map((value) => ({
                    services: [BleServiceUUIDs.get(value) as BluetoothServiceUUID],
                })),
                optionalServices: [0xFE59, 0x180F], // DFU and Battery services
            });

            const newDeviceHandler = new DeviceHandler(
                0,
                device,
                this.onDeviceConnect.bind(this),
                this.onDeviceDisconnect.bind(this),
                this.onDeviceUpdate.bind(this)
            );

            console.log("Device not ready:", newDeviceHandler);
            await newDeviceHandler.waitForDeviceReady();
            console.log("Device ready:", newDeviceHandler);

            try{
                newDeviceHandler.setPackageUpdateStartCallback((e: any) => {
                    console.log("Firmware update started:", e);
                });

                newDeviceHandler.setPackageUpdateProgressCallback((e: { progress: number }) => {
                    console.log("Firmware update progress:", e.progress);
                    this.progress = e.progress;
                });

                newDeviceHandler.setPackageUpdateFinishCallback((e: any) => {
                    console.log("Firmware update finished:", e);
                    this.progress = 100;
                });
            } catch (error) {
                console.error("Error setting dfu callbacks:", error);
            }

            // Disconnect any existing handler
            if (this.deviceHandler) {
                this.deviceHandler.disconnect();
            }

            this.deviceHandler = newDeviceHandler;
            this.connectionStatus = "connected";
            console.log("Device connected successfully!");
            return newDeviceHandler;
        } catch (error) {
            console.error("Error connecting to device:", error);
            this.connectionStatus = "disconnected";
            return null;
        }
    }

    // Event handler for device connection
    private onDeviceConnect(): void {
        this.connectionStatus = "connected";
        console.log("Device connected!", this.deviceHandler);
    }

    // Event handler for device disconnection
    private onDeviceDisconnect(): void {
        this.connectionStatus = "disconnected";
        console.log("Device disconnected!", this.deviceHandler);
    }

    // Event handler for device updates
    private onDeviceUpdate(e: Record<string, any>): void {
        if (this.onDeviceUpdateHandler && this.deviceHandler) {
            this.onDeviceUpdateHandler(this.deviceHandler);
        }
    }
}