import { DeviceHandler } from "@mtrust/console-kit";
import Peer, { DataConnection } from "peerjs";
// @ts-ignore
import { UrpMessage } from '@mtrust/urp-types/types';

type PeerConnectionCallbacks = {
  onOpen?: (id: string) => void;
  onError?: (error: Error) => void;
  onClose?: () => void;
};

export class PeerConnection {
  private seqNr: number = 0;
  private static instance: PeerConnection | null = null;
  private peerInstance: Peer;

  // Store connections
  private cmdChannel: DataConnection | null = null;
  private deviceChannel: DataConnection | null = null;
  private packageChannel: DataConnection | null = null;

  private onConnectionUpdateCallback: (connected: boolean) => void = () => { };
  public onConnectionUpdate(callback: (connected: boolean) => void): void {
    this.onConnectionUpdateCallback = callback;
  }

  private onCmdChannelCallback: (data: Uint8Array) => void = () => { };
  public onCmdChannel(callback: (data: Uint8Array) => void): void {
    this.onCmdChannelCallback = callback;
  }

  private onPackageChannelCallback: (data: Object) => void = () => { };
  public onPackageChannel(callback: (data: Object) => void): void {
    this.onPackageChannelCallback = callback;
  }

  private onDeviceChannelCallback: (device: DeviceHandler) => void = () => { };
  public onDeviceChannel(callback: (device: DeviceHandler) => void): void {
    this.onDeviceChannelCallback = callback;
  }

  private connectedDevice: DeviceHandler | null = null;

  public setConnectedDevice(device: DeviceHandler): void {
    this.connectedDevice = device;
  }

  private _intToBytes = (val: number): number[] => {
    const lsb = val & 0xFF;
    const msb = (val >> 8) & 0xFF;
    return [msb, lsb];
  };

  private _pack = (data: Uint8Array): Uint8Array => {
    const bytes = this._intToBytes(data.length);
    return new Uint8Array([...bytes, ...data]);
  };

  private constructor(private callbacks: PeerConnectionCallbacks = {}) {
    const peerId = this.generateShortId();
    
    this.peerInstance = new Peer(peerId, {
      //debug: 3,
      config: {
      iceServers: [
        // Free STUN servers
        { urls: "stun:stun.l.google.com:19302" },
        { urls: "stun:stun1.l.google.com:19302" },
        { urls: "stun:stun2.l.google.com:19302" },
        { urls: "stun:stun3.l.google.com:19302" },
        { urls: "stun:stun4.l.google.com:19302" },
        { urls: "stun:stun.stunprotocol.org:3478" },
        { urls: "stun:stun.nextcloud.com:3478" },
        // Free TURN servers (note: public/free TURN servers are rare and may be unreliable)
        // No free public TURN servers are recommended for production use.
        // For testing, you may use the following demo server (subject to rate limits and availability):
        {
          urls: "turn:numb.viagenie.ca",
          username: "webrtc@live.com",
          credential: "muazkh"
        },
        // You may also use the following (also subject to availability):
        {
          urls: "turn:turn.anyfirewall.com:443?transport=tcp",
          username: "webrtc",
          credential: "webrtc"
        },
        // Existing servers
        {
        urls: "stun:stun.relay.metered.ca:80",
        },
        {
        urls: "turn:global.relay.metered.ca:80",
        username: "51215e95308d947ef41d81cc",
        credential: "ZwWlCwuJHNmoh85i",
        },
        {
        urls: "turn:global.relay.metered.ca:80?transport=tcp",
        username: "51215e95308d947ef41d81cc",
        credential: "ZwWlCwuJHNmoh85i",
        },
        {
        urls: "turn:global.relay.metered.ca:443",
        username: "51215e95308d947ef41d81cc",
        credential: "ZwWlCwuJHNmoh85i",
        },
        {
        urls: "turns:global.relay.metered.ca:443?transport=tcp",
        username: "51215e95308d947ef41d81cc",
        credential: "ZwWlCwuJHNmoh85i",
        },
      ],
      }
    });

    this.peerInstance.on("open", (id) => {
      this.callbacks.onOpen?.(id);
    });

    this.peerInstance.on("connection", (connection) => {
      this.onConnectionUpdateCallback(true);
      this.storeConnection(connection);
      this.handleConnection(connection);
    });

    this.peerInstance.on("error", (err) => {
      console.error("Peer error:", err);
      this.onConnectionUpdateCallback(false);
      this.callbacks.onError?.(err);
    });

    this.peerInstance.on("close", () => {
      this.clearConnections();
      this.onConnectionUpdateCallback(false);
    });

    this.peerInstance.on("disconnected", () => {
      this.peerInstance.reconnect();
    });
  }

  public static getInstance(callbacks: PeerConnectionCallbacks = {}): PeerConnection {
    if (!PeerConnection.instance) {
      PeerConnection.instance = new PeerConnection(callbacks);
    }
    return PeerConnection.instance;
  }

  private generateShortId(length = 6): string {
    const chars = "ABCDEFGHIJKLMNOPQRSTUVWXYZ0123456789";
    return Array.from({ length }, () => chars.charAt(Math.floor(Math.random() * chars.length))).join("");
  }

  private storeConnection(connection: DataConnection): void {
    
    switch (connection.label) {
      case "cmdChannel":
        this.cmdChannel = connection;
        break;
      case "deviceChannel":
        this.deviceChannel = connection;
        break;
      case "packageChannel":
        this.packageChannel = connection;
        break;
      default:
        console.warn(`Unknown connection label: ${connection.label}`);
    }
  }

  private clearConnections(): void {
    
    if (this.cmdChannel?.open) {
      this.cmdChannel.close();
    }
    if (this.deviceChannel?.open) {
      this.deviceChannel.close();
    }
    if (this.packageChannel?.open) {
      this.packageChannel.close();
    }
    
    this.cmdChannel = null;
    this.deviceChannel = null;
    this.packageChannel = null;
  }

  private handleConnection(connection: DataConnection): void {

    // Set up common handlers
    connection.on("open", () => {
      this.onConnectionUpdateCallback(true);
    });

    connection.on("close", () => {
      this.onConnectionUpdateCallback(false);
    });

    connection.on("error", (error) => {
      console.error(`${connection.label} error:`, error);
      this.onConnectionUpdateCallback(false);
    });

    // Set up data handlers
    switch (connection.label) {
      case "cmdChannel":
        connection.on("data", (data: any) => {
          if (this.onCmdChannelCallback) {
            this.onCmdChannelCallback(data);
          }
        });
        break;

      case "deviceChannel":
        connection.on("data", (data: any) => {
          try {
            if (this.onDeviceChannelCallback) {
              const parsedData = typeof data === 'string' ? JSON.parse(data) : data;
              this.onDeviceChannelCallback(parsedData);
            }
          } catch (error) {
            console.error("Error handling device data:", error);
          }
        });
        break;

      case "packageChannel":
        connection.on("data", (data: any) => {
          if (this.onPackageChannelCallback) {
            this.onPackageChannelCallback(data);
          }
        });
        break;
    }
  }

  public connectToPeer(peerId: string): void {
    if (!peerId) {
      console.error("No peer ID provided");
      return;
    }

    if (peerId === this.peerInstance.id) {
      console.error("Cannot connect to self");
      return;
    }
    
    try {
      // Clean up any existing connections
      this.clearConnections();

      const commonOptions = {
        reliable: true,
        metadata: { version: "1.0.0" }
      };

      // Create channels
      this.cmdChannel = this.peerInstance.connect(peerId, { 
        ...commonOptions,
        label: "cmdChannel",
        serialization: "binary"
      });

      this.deviceChannel = this.peerInstance.connect(peerId, {
        ...commonOptions,
        label: "deviceChannel",
        serialization: "binary"
      });

      this.packageChannel = this.peerInstance.connect(peerId, {
        ...commonOptions,
        label: "packageChannel",
        serialization: "binary"
      });

      // Set up handlers for each channel
      [this.cmdChannel, this.deviceChannel, this.packageChannel].forEach(channel => {
        if (!channel) {
          throw new Error("Failed to create channel");
        }

        this.handleConnection(channel);
      });

    } catch (error) {
      console.error("Error establishing peer connections:", error);
      this.onConnectionUpdateCallback(false);
      this.clearConnections();
      throw error;
    }
  }

  public disconnect(): void {
    this.clearConnections();
    this.peerInstance.destroy();
    PeerConnection.instance = null;
  }

  public getPeerId(): string {
    return this.peerInstance.id;
  }

  public isConnected(): boolean {
    return (
      this.peerInstance &&
      !this.peerInstance.disconnected &&
      ((this.cmdChannel?.open ?? false) ||
      (this.deviceChannel?.open ?? false) ||
      (this.packageChannel?.open ?? false))
    );
  }

  public transmitDeviceHandler(handler: DeviceHandler): void {
    if (!this.deviceChannel?.open) {
      console.error("Device channel not ready");
      return;
    }

    try {
      const serializedData = JSON.stringify(handler);
      this.deviceChannel.send(serializedData);
    } catch (error) {
      console.error("Error transmitting device data:", error);
    }
  }

  public transmitPackage(data: object): void {
    if (!this.packageChannel?.open) {
      console.error("Package channel not ready");
      return;
    }

    try {
      this.packageChannel.send(data);
    } catch (error) {
      console.error("Error transmitting package:", error);
    }
  }

  public async transmitCmd(msg: UrpMessage): Promise<void> {
    if (!this.cmdChannel?.open) {
      console.error("Command channel not ready");
      return;
    }

    try {
      await this.cmdChannel.send(msg.serializeBinary());
    } catch (error) {
      console.error("Error transmitting command:", error);
    }
  }

  public async transmitRawCmd(data: Uint8Array): Promise<void> {
    if (!this.cmdChannel?.open) {
      console.error("Command channel not ready");
      return;
    }

    try {
      await this.cmdChannel.send(data);
    } catch (error) {
      console.error("Error transmitting raw command:", error);
    }
  }
}
