import {
    UrpSecCommandWrapper,
    UrpSecDeviceCommand,
    UrpSecCommand,
    // @ts-ignore
} from '@mtrust/urp-types/sec_pb';

import {
    UrpTokenRequest,
    UrpSecureToken
    // @ts-ignore
} from "@mtrust/urp-types/token_pb";

import {
    UrpMessage,
    UrpMessageHeader,
    UrpRequest
    // @ts-ignore
} from "@mtrust/urp-types/wrapper_pb";

import {
    CommandBuilder,
} from './command_builder';

// Usage
const secCmdBuilder = new CommandBuilder(UrpSecCommandWrapper, UrpSecDeviceCommand);

export const secGetModelInfoMsg = () => {
    return secCmdBuilder.buildMessage(UrpSecCommand.URPSECGETMODELINFO);
}

export const secGetTokenMsg = () => {
    return secCmdBuilder.buildMessage(UrpSecCommand.URPSECGETTOKEN);
}

export const secRequestTokenMsg = (amount = 10) => {
    const tokenRequest = new UrpTokenRequest();
    tokenRequest.setAmount(amount);

    const msg = new UrpMessage();
    const header = new UrpMessageHeader();
    msg.setHeader(header);

    const wrapper = new UrpSecCommandWrapper();
    const cmd = new UrpSecDeviceCommand();
    cmd.setCommand(UrpSecCommand.URPSECREQUESTTOKEN);
    cmd.setTokenrequest(tokenRequest);

    wrapper.setDevicecommand(cmd);

    const request = new UrpRequest();
    request.setPayload(wrapper.serializeBinary());
    msg.setRequest(request);
    return msg;
}

export const secSetTokenMsg = (token: UrpSecureToken) => {
    const msg = new UrpMessage();
    const header = new UrpMessageHeader();
    msg.setHeader(header);

    const wrapper = new UrpSecCommandWrapper();
    const cmd = new UrpSecDeviceCommand();

    cmd.setCommand(UrpSecCommand.URPSECSETTOKEN);
    cmd.setSecuretoken(token);

    wrapper.setDevicecommand(cmd);
    
    const request = new UrpRequest();
    request.setPayload(wrapper.serializeBinary());
    msg.setRequest(request);
    return msg;
}

export const secForwardMsg = (serialized_wrapper: Uint8Array) => {
    const msg = new UrpMessage();
    const header = new UrpMessageHeader();
    msg.setHeader(header);
    
    const request = new UrpRequest();
    request.setPayload(serialized_wrapper);
    msg.setRequest(request);
    return msg;
}