import { useEffect, useState, useRef } from 'react';
import { BleConnection } from '../drivers/bleConnector';
import { DeviceHandler } from '@mtrust/console-kit';

// @ts-ignore
import { UrpMessage } from '@mtrust/urp-types/wrapper_pb';
import { PeerConnection } from '../drivers/peerConnection';
import { LdIcon, LdCard, LdLoading } from '@emdgroup-liquid/liquid/dist/react';
import '@emdgroup-liquid/liquid/dist/css/liquid.global.css'

import styled from "styled-components";

export const List = styled.div`
    list-style-type: none;
    ld-loading {
        height: 1rem; 
        margin-left: 3px;
        margin-right: 3px;
        width: 1rem;
        position: relative;
        top: 2px;
    }
    ld-icon{
        scale: 0.7;
        height: 1rem;    
        width: 1rem;
        position: relative;
        top: 4px;
        left: 3px;
        margin-right: 5px;
    }
`;

function CustomerPage() {

    const [connectedPeerId, setConnectedPeerId] = useState<string>("");
    const [enteredConnectedPeerId, setEnteredConnectedPeerId] = useState<string>("");
    const deviceHandlerRef = useRef<DeviceHandler | null>(null);
    const [deviceHandler, setDeviceHandler] = useState<DeviceHandler | null>(null);

    const [connected, setConnected] = useState(false);
    const [peerConnection, setPeerConnection] = useState<PeerConnection | null>(null);

    useEffect(() => {
        const queryString = window.location.hash.split('?')[1];
        const params = new URLSearchParams(queryString);
        setConnectedPeerId(params.get('id') || "");
        console.log(params);
    }, []);

    // Callback called whenever the ble device is updated
    const onDeviceUpdateCallback = (d: DeviceHandler): void => {
        if (!peerConnection?.isConnected()) {
            console.warn("deviceChannel not open!");
            return;
        }
        deviceHandlerRef.current = d;
        setDeviceHandler(d);

        peerConnection.transmitDeviceHandler(d);
    }

    // Initialize BleConnector
    const bleDeviceConnector = new BleConnection(onDeviceUpdateCallback);

    // Setup peer connection
    useEffect(() => {
        const peerConnection = PeerConnection.getInstance({
            onError: (error) => {
                console.error("Peer connection error:", error);
            },
        });

        const handlePackageChannelUpdate = (data: any) => {
            const currentDevice = deviceHandlerRef.current;
            if (currentDevice) {
                
                if (data.variant === 0x01) { // Firmware update
                    currentDevice.uploadFirmware(new File([data.data], "firmware.bin"), data.reboot);
                    peerConnection.transmitDeviceHandler(currentDevice);
                } else if(data.variant === 0x02) {
                    currentDevice.uploadModel(new File([data.data], "model.bin"), data.reboot);
                }
            } else {
                console.warn("No device handler available for package update");
            }
        };

        const handleCmdChannelUpdate = async (request_payload: any) => {
            try {
                const data = new Uint8Array(request_payload);

                const currentDevice = deviceHandlerRef.current;

                if (currentDevice) {
                    currentDevice.forwardCmd(data, (res: any) => {
                        // send the raw data back to the peer connection

                        peerConnection?.transmitRawCmd(res);
                    });
                } else {
                    console.warn("No current device available to forward token.");
                }
            } catch (error) {
                console.error("Error in handleCmdChannelUpdate:", error);
            }
        }; // Added missing closing bracket

        peerConnection.onCmdChannel(handleCmdChannelUpdate);
        peerConnection.onPackageChannel(handlePackageChannelUpdate);
        peerConnection.onConnectionUpdate((connected: boolean) => {
            setConnected(connected);
        });
        
        setPeerConnection(peerConnection);

        return () => {
            if (peerConnection) {
                peerConnection.disconnect();
            }
        };
    }, []); // Empty dependency array since getInstance is a singleton

    const connectToPeer = () => {
        setConnectedPeerId(enteredConnectedPeerId);
        const id = connectedPeerId || enteredConnectedPeerId;
        
        if (!id) {
            alert("Enter a valid Peer ID!");
            return;
        }

        peerConnection?.connectToPeer(id);
    };

    const log = (message: UrpMessage): void => {
        if (!peerConnection) {
            console.error("Peer connection is not initialized.");
            return;
        }
        peerConnection?.transmitRawCmd(message);
    }

    // Function to connect to the BLE device
    const connectDevice = async (): Promise<void> => {
        // Allow all mtrust devices

        // Search for BLE device
        const d = await bleDeviceConnector!.connectBleDevice([4, 6, 7]);
  
        d?.setLogCallback(log);

        d?.setAutoReconnect(true);
        d?.setReconnectCallback((d: DeviceHandler) => {
            console.log("Device reconnected!", d);
            deviceHandlerRef.current = d;
            setDeviceHandler(d);
            peerConnection?.transmitDeviceHandler(d);
        });
        d?.setPackageUpdateProgressCallback((e: { progress: number }) => {
            onDeviceUpdateCallback(deviceHandlerRef.current? deviceHandlerRef.current : d);
        });

        if (!d) {
            console.warn("No device found!");
            return;
        }
        deviceHandlerRef.current = d;
        setDeviceHandler(d);
        if (d) {
            peerConnection?.setConnectedDevice(d);
        }
    }

    return (
        <LdCard>
            <h3>Web Customer Support</h3>

            <List>
                {connected ? <li><LdIcon name="checkmark" />Peer connection established</li> : <li><LdLoading />Waiting for connection to support...</li>}
                {deviceHandler ? <li><LdIcon name="checkmark" />Customer device connected</li> : <li><LdLoading />Waiting for device connection...</li>}
                {connected && deviceHandler ? <><br/><li> You are set, please wait for the support do its thing...</li></> : ""}
            </List>

            {!connected ? (
                <>
                    {connectedPeerId ? (
                        <p>PeerId <strong>{connectedPeerId}</strong> was injected by support url.</p>
                    ) : (
                        <>
                        <p>Ask your supporter for the host id.</p>
                        <input value={enteredConnectedPeerId}
                        placeholder='<host peer id>'
                        onChange={(e) => setEnteredConnectedPeerId(e.target.value)} ></input>
                        </>
                    )}
                    
                    <button
                        color="primary"
                        onClick={connectToPeer}>Connect to Host</button>

            </>) : (
                !deviceHandler && (
                <>
                <p>Connect your device via BLE.</p>
                <button onClick={connectDevice}>Connect device</button>
                </>)
            )}
        </LdCard>
    );
}

export default CustomerPage;
