import { useEffect, useState } from 'react';
import { DeviceHandler } from '@mtrust/console-kit';
import { PeerConnection } from '../drivers/peerConnection';
import { LdButton, LdLabel, LdInput, LdIcon, LdLoading, LdCard, LdInputMessage } from '@emdgroup-liquid/liquid/dist/react'
import '@emdgroup-liquid/liquid/dist/css/liquid.global.css'

// @ts-ignore
import {UrpSecCommand, UrpSecCommandWrapper, UrpSecDeviceCommand} from '@mtrust/urp-types/sec_pb';
// @ts-ignore
import {UrpImpCommand, UrpImpCommandWrapper, UrpImpDeviceCommand} from '@mtrust/urp-types/imp_pb';
// @ts-ignore
import {UrpCoreCommand, UrpCommand } from '@mtrust/urp-types/wrapper_pb';
// @ts-ignore
import {UrpSecureToken} from '@mtrust/urp-types/token_pb';

import styled from "styled-components";


export const List = styled.div`
    list-style-type: none;
    ld-loading {
        height: 1rem; 
        margin-left: 3px;
        margin-right: 3px;
        width: 1rem;
        position: relative;
        top: 2px;
    }
    ld-icon{
        scale: 0.7;
        height: 1rem;    
        width: 1rem;
        position: relative;
        top: 4px;
        left: 3px;
        margin-right: 5px;
    }
`;

const DeviceManager = styled.div`
    display: flex;
    flex-direction: column;
    align-items: left;
    justify-content: center;
    margin-top: 20px;

    table{
        border-collapse: collapse;
        width: 100%;
    }

    td, th {
        padding: 0;
        padding-right: 10px;
        padding-bottom: 5px;
        font-family: monospace, sans-serif;
        font-size: 0.8rem;
    }
`;

function SupportPage() {
    const [deviceHandler, setDeviceHandler] = useState<DeviceHandler | null>(null);
    const [fwFile, setFwFile] = useState<File | null>(null);
    const [modelFile, setModelFile] = useState<File | null>(null);

    const [connected, setConnected] = useState(false);
    const [peerConnection, setPeerConnection] = useState<PeerConnection | null>(null);

    const [deviceManager, setDeviceManager] = useState<{
        connected: boolean;
        interval: NodeJS.Timeout | null;
        environment: string | null;
    }>({
        connected: false,
        interval: null,
        environment: null,
    });

    const handleCmdChannelUpdate = async (request_payload: any) => {
        console.log("Command Channel Update", request_payload);
    }

    useEffect(() => {
        const peerConnection = PeerConnection.getInstance({
            onError: (error) => {
                console.error("Peer connection error:", error);
            },
        });

        peerConnection.onDeviceChannel((device: any) => {
            // Create a new instance using the factory method
            const revivedDevice = DeviceHandler.fromSerialization(device);
            setDeviceHandler(revivedDevice);
        });

        peerConnection.onConnectionUpdate((connected: boolean) => {
            setConnected(connected);
        });

        peerConnection.onCmdChannel(handleCmdChannelUpdate);

        const interval = setInterval(async () => {
            try {
                const response = await fetch('http://localhost:8000/health', {
                    method: 'GET',
                    headers: {
                        'Content-Type': 'application/json',
                    },
                });

                if (response.ok) {
                    const body = await response.json();
                    setDeviceManager({
                        ...deviceManager,
                        connected: true,
                        environment: body.env,
                    });
                } else {
                    setDeviceManager({
                        ...deviceManager,
                        connected: false
                    });
                }
            }
            catch (error) {
                console.error("Error checking device manager connection:", error);
                setDeviceManager({
                        ...deviceManager,
                        connected: false
                });
            }
        }, 5000); // Check every 5 seconds
        setDeviceManager({
            ...deviceManager,
            interval: interval
        });

        setPeerConnection(peerConnection);

        return () => {
            peerConnection.disconnect(); // Clean up on unmount
            if (deviceManager.interval) {
                clearInterval(deviceManager.interval);
            }
        };
        // eslint-disable-next-line
    }, []);

    useEffect(() => {
        console.log("Connected state updated:", connected);
    }, [connected]);


    const sendFirmwareUpdate = async () => {
        if (!peerConnection?.isConnected()) {
            console.warn("packageChannel not open!");
            return;
        }
        if (!fwFile) {
            console.warn("No firmware file selected!");
            return;
        }
        const data = await fwFile.arrayBuffer();
        peerConnection.transmitPackage({ variant: 0x01, data: data, reboot: true });
    }

    const sendModelUpdate = async () => {
        if (!peerConnection?.isConnected()) {
            console.warn("packageChannel not open!");
            return;
        }
        if (!modelFile) {
            console.warn("No model file selected!");
            return;
        }
        const data = await modelFile.arrayBuffer();
        peerConnection.transmitPackage({ variant: 0x02, data: data, reboot: true });
    }

    const rebootDevice = async () => {
        if (!peerConnection?.isConnected()) {
            console.warn("cmdChannel not open!");
            return;
        }
        if (!deviceHandler) {
            console.warn("No device handler available to reboot device!");
            return;
        }
        const wrapper = new UrpSecCommandWrapper();
        const cmd = new UrpCoreCommand();
        cmd.setCommand(UrpCommand.URPREBOOT);
        
        wrapper.setCorecommand(cmd);
        const request = wrapper.serializeBinary();
            
        console.log(request);
        // this method can basically be used to inject any comand into the device
        await peerConnection?.transmitRawCmd(request);
    }

    const getToken = async () => {
        if (!peerConnection?.isConnected()) {
            console.warn("cmdChannel not open!");
            return;
        }
        if (!deviceHandler) {
            console.warn("No device handler available to create token!");
            return;
        }
        if(deviceHandler.isSec()){
            const wrapper = new UrpSecCommandWrapper();
            const cmd = new UrpSecDeviceCommand();
            cmd.setCommand(UrpSecCommand.URPSECGETTOKEN);
        
            wrapper.setDevicecommand(cmd);
            const request = wrapper.serializeBinary();
            
            console.log(request);
            // this method can basically be used to inject any comand into the device
            await peerConnection?.transmitRawCmd(request);
        } else if(deviceHandler.isImp()){
            const wrapper = new UrpImpCommandWrapper();
            const cmd = new UrpImpDeviceCommand();
            cmd.setCommand(UrpImpCommand.URPIMPGETTOKEN);
        
            wrapper.setDevicecommand(cmd);
            const request = wrapper.serializeBinary();
            
            console.log(request);
            // this method can basically be used to inject any comand into the device
            await peerConnection?.transmitRawCmd(request);
        } else {
            console.warn("Not implemented for device type!");
        }
    }

    const createAndSendToken = async () => {
        if (!peerConnection?.isConnected()) {
            console.warn("cmdChannel not open!");
            //return;
        }
        if (!deviceHandler) {
            console.warn("No device handler available to create token!");
            return;
        }
        try {
            // do a rest call to localhost:8080/api/v1/token/create/aa8983ae1a6ab51318e865896df897b2
            const response = await fetch('http://localhost:8000/api/v1/token/create/' + deviceHandler.info?.id + '?deviceType=SEC', {
                method: 'GET',
                headers: {
                    'Content-Type': 'application/octet-stream',
                },
            });

            if (!response.ok) {
                throw new Error(`HTTP error! status: ${response.status}`);
            }
            const raw: Uint8Array = response.body ? new Uint8Array(await response.arrayBuffer()) : new Uint8Array();

            // check if the received token is in the correct format
            const token = UrpSecureToken.deserializeBinary(raw);
            console.log(token);

            // pack token into a install request
            if(deviceHandler.isSec()){
                const wrapper = new UrpSecCommandWrapper();
                const cmd = new UrpSecDeviceCommand();
                cmd.setCommand(UrpSecCommand.URPSECSETTOKEN);
                cmd.setSecuretoken(token);
            
                wrapper.setDevicecommand(cmd);
                const request = wrapper.serializeBinary();
            
                console.log(request);
                // this method can basically be used to inject any comand into the device
                await peerConnection?.transmitRawCmd(request);
            } else if(deviceHandler.isImp()){
                const wrapper = new UrpImpCommandWrapper();
                const cmd = new UrpImpDeviceCommand();
                cmd.setCommand(UrpImpCommand.URPIMPSETTOKEN);
                cmd.setSecuretoken(token);
            
                wrapper.setDevicecommand(cmd);
                const request = wrapper.serializeBinary();
            
                console.log(request);
                // this method can basically be used to inject any comand into the device
                await peerConnection?.transmitRawCmd(request);
            }
            else {
                console.warn("Not implemented for device type!");
            }

        } catch (error) {
            console.error("Error creating token:", error);
        }
    }

    const copyUrl = () => {
        const url = "https://emdgroup.github.io/mtrust-web-support/#/?id=" + peerConnection?.getPeerId();
        navigator.clipboard.writeText(url).then(() => {
            alert("Session URL copied to clipboard: " + url);
        }
        )
    }

    return (
        <LdCard>
            {!connected ?
                <LdLabel>
                    Session ID:
                    <LdInput size='lg' disabled value={peerConnection?.getPeerId()}></LdInput>
                    <LdInputMessage mode="info">
                        <p onClick={copyUrl}>Share this url with your customer to support.<br/>Click to copy.</p>
                    </LdInputMessage>
                </LdLabel> : <></>
            }

            <List>
                {connected ? <li><LdIcon name="checkmark" />Peer connection established</li> : <li><LdLoading />Waiting for customer to connect...</li>}
                {deviceHandler ? <li><LdIcon name="checkmark" />Customer device connected</li> : <li><LdLoading />Waiting for device connection...</li>}
                {deviceManager.connected ? <li><LdIcon name="checkmark" />Device manager connected in {deviceManager.environment}</li> : <li><LdLoading />Waiting for device manager...</li>}
            </List>

            {deviceHandler && connected?
                <DeviceManager>
                    <table>
                        <th>
                            <td colSpan={2}>
                                <LdLabel>Device Information:</LdLabel>
                            </td>
                        </th>
                        <tr>
                            <td><LdLabel>Serial:</LdLabel></td>
                            <td>{deviceHandler!.info?.name}</td>
                        </tr>
                        
                        <tr>
                            <td><LdLabel>Device-ID:</LdLabel></td>
                            <td>{deviceHandler!.info?.id}</td>
                        </tr>
                        <tr>
                            <td><LdLabel>Device-Type:</LdLabel></td>
                            <td>{deviceHandler!.info?.devicetype}</td>
                        </tr>
                        <tr>
                            <td><LdLabel>Device-Mac:</LdLabel></td>
                            <td>{deviceHandler!.info?.mac}</td>
                        </tr>
                        <tr>
                            <td><LdLabel>Battery:</LdLabel></td>
                            <td>{deviceHandler!.batteryLevel() != null ? Number(deviceHandler!.batteryLevel()).toString() + "%" : 'unknown'}</td>
                        </tr>
                        <tr>
                            <td><LdLabel>Public-Key:</LdLabel></td>
                            <td><pre>{deviceHandler!.publicKey()}</pre></td>
                        </tr>

                        {deviceHandler!.isSec() || deviceHandler.isImz() ? ( <>
                        <th>
                            <td colSpan={2}>
                                <br />
                                <LdLabel>Model Information:</LdLabel>
                            </td>
                        </th>
                        <tr>
                            <td><LdLabel>Installed ({deviceHandler!.models().length}):</LdLabel></td>
                            <td>
                                {Array.isArray(deviceHandler!.models())
                                    ? deviceHandler!.models().map((model: any) => model.modelid + " (" + model.version + ")").join(', ')
                                    : String(deviceHandler!.models())}
                            </td>
                        </tr>
                        <tr>
                            <td><LdLabel>
                                Update Model:</LdLabel>
                            </td>
                            <td>
                                <LdInput disabled={false} size="sm" placeholder="Select Model File" type="file" id="fileInput" onLdchange={(e) => setModelFile(e.target.files?.length! > 0 ? e.target.files![0] : null)}>
                                    <LdButton disabled={modelFile ? false : true} mode="ghost" slot="end" aria-label="Submit" onClick={sendModelUpdate}>
                                        <LdIcon name="upload" />
                                    </LdButton>
                                </LdInput>
                            </td>
                        </tr>
                        </>) : <></>}

                        
                        {deviceHandler!.token() == null ? 
                        <th>
                            <td colSpan={2}>
                                <br />
                                <LdLabel>Loading Token Information...</LdLabel>
                            </td>
                        </th> : 
                        <>
                        <th>
                            <td colSpan={2}>
                                <br />
                                <LdLabel>Token Information:</LdLabel>
                            </td>
                        </th>
                        <tr>
                            <td><LdLabel>Token Serial:</LdLabel></td>
                            <td>{deviceHandler!.token()!.sn}</td>
                        </tr>
                        <tr>
                            <td><LdLabel>Token Active Count:</LdLabel></td>
                            <td>{deviceHandler!.token()!.active}</td>
                        </tr>
                        <tr>
                            <td><LdLabel>Token Created Date:</LdLabel></td>
                            <td>{deviceHandler!.token()!.iat ? new Date(deviceHandler!.token()!.iat * 1000).toLocaleString() : 'unknown'}</td>
                        </tr>
                        <tr>
                            <td><LdLabel>Token Expiring Date:</LdLabel></td>
                            <td>{deviceHandler!.token()!.exp ? new Date(deviceHandler!.token()!.exp * 1000).toLocaleString() : 'unknown'}</td>
                        </tr>
                        </>
                        }
                        <th>
                            <td colSpan={2}>Token operations:</td>
                        </th>
                        <tr>
                            <td><LdLabel>(Re-)Create Token</LdLabel></td>
                            <td>
                                <LdButton mode="highlight" onClick={createAndSendToken}>
                                    Create and Install
                                </LdButton>
                                &nbsp;
                                <LdButton mode="secondary" onClick={getToken}>
                                    Get Token
                                </LdButton>
                            </td>
                        </tr>


                        <th>
                            <td colSpan={2}>
                                <br />
                                <LdLabel>Firmware Information:</LdLabel>
                            </td>
                        </th>
                        <tr>
                            <td><LdLabel>Firmware:</LdLabel></td>
                            <td>{deviceHandler!.info?.fwVersion}</td>
                        </tr>
                        <tr>
                            <td><LdLabel>Firmware Type:</LdLabel></td>
                            <td>{deviceHandler!.info?.fwType}</td>
                        </tr>
                        <tr>
                            <td><LdLabel>Firmware MD5:</LdLabel></td>
                            <td>{deviceHandler!.info?.fwMd5}</td>
                        </tr>
                        <tr>
                            <td><LdLabel>Firmware Update in progress:</LdLabel></td>
                            <td>
                                {deviceHandler!.dfuHandler()?.updateInProgress() ?
                                    (typeof deviceHandler!.dfuHandler()?.percentage === 'function'
                                        ? (() => {
                                            const percent = deviceHandler!.dfuHandler()?.percentage();
                                            return typeof percent === 'number'
                                                ? Math.round(percent) + "%"
                                                : "Unknown";
                                        })()
                                        : "Unknown")
                                    : "No update"}
                            </td>
                        </tr>
                        <tr>
                            <td>
                                <LdLabel>Update Firmware:</LdLabel></td>
                            <td>
                                <LdInput disabled={false} size="sm" placeholder="Select Firmware File" type="file" id="fileInput" onLdchange={(e) => setFwFile(e.target.files?.length! > 0 ? e.target.files![0] : null)}>
                                    <LdButton disabled={fwFile ? false : true} mode="ghost" slot="end" aria-label="Submit" onClick={sendFirmwareUpdate}>
                                        <LdIcon name="upload" />
                                    </LdButton>
                                </LdInput>
                            </td>
                        </tr>

                        <tr>
                            <td><LdLabel>Commands:</LdLabel></td>
                            <td>
                                <LdButton mode="secondary" onClick={rebootDevice}>
                                    Reboot Device
                                </LdButton>
                            </td>
                        </tr>
                    </table>
                </DeviceManager>
                : <></>}
        </LdCard>
    );
}

export default SupportPage;
