/**
 * Handles the Device Firmware Update (DFU) process over Bluetooth.
 */
export class DfuHandler {

    private _updateInProgress: boolean;
    private _dfuChannel: BluetoothRemoteGATTCharacteristic | null;

    private _cancel: boolean;
    private _percentage: number;

    private _startCallback: ((e: { fileSize: number, chunkSize: number, totalChunks: number }) => void) | null;
    private _updateCallback: ((e: { progress: number, totalChunks: number, remainingChunks: number }) => void) | null;
    private _endCallback: ((e: { rebootNeeded: boolean, error?: any }) => void) | null;

    /**
     * Creates an instance of DfuHandler.
     * @param {BluetoothRemoteGATTCharacteristic} dfuChannel - The Bluetooth GATT characteristic used for DFU.
     */
    constructor(dfuChannel: BluetoothRemoteGATTCharacteristic | null) {
        this._updateInProgress = false;
        this._cancel = false;
        this._dfuChannel = dfuChannel;
        this._startCallback = null;
        this._updateCallback = null;
        this._endCallback = null;
        this._percentage = 0;
    }

    static fromSerialization = (data: any) => {
        if (data) {
            const handler = new DfuHandler(null);
            handler._updateInProgress = data._updateInProgress || false;
            handler._cancel = data._cancel || false;
            handler._startCallback = data._startCallback || null;
            handler._updateCallback = data._updateCallback || null;
            handler._endCallback = data._endCallback || null;
            handler._percentage = data._percentage || 0;
            return handler;
        }
        throw new Error("Invalid serialization data for DfuHandler");
    }

    /**
     * Sets the callback function to be invoked when the firmware update starts.
     * @param {(e: { fileSize: number, chunkSize: number, totalChunks: number }) => void} startCallback - Callback function for the start event.
     */
    setStartCallback = (startCallback: (e: { fileSize: number, chunkSize: number, totalChunks: number }) => void) => {
        this._startCallback = startCallback;
    }

    /**
     * Sets the callback function to be invoked during the firmware update.
     * @param {(e: { progress: number, totalChunks: number, remainingChunks: number }) => void} updateCallback - Callback function for the update event.
     */
    setUpdateCallback = (updateCallback: (e: { progress: number, totalChunks: number, remainingChunks: number }) => void) => {
        this._updateCallback = updateCallback;
    }

    percentage = () => {
        return this._percentage;
    }

    /**
     * Sets the callback function to be invoked when the firmware update ends.
     * @param {(e: { rebootNeeded: boolean, error?: any }) => void} endCallback - Callback function for the end event.
     */
    setEndCallback = (endCallback: (e: { rebootNeeded: boolean, error?: any }) => void) => {
        this._endCallback = endCallback;
    }

    /**
     * Handles firmware upload process.
     * @param {File} file - The firmware file to be uploaded.
     * @param {boolean} autoReboot - Indicates whether the device should reboot automatically after update.
     */
    handleFirmwareUpload = async (file: File, autoReboot: boolean) => {
        this.uploadPackage(file, 0x01, autoReboot);
    }

    /**
     * Handles model upload process.
     * @param {File} file - The model file to be uploaded.
     * @param {boolean} autoReboot - Indicates whether the device should reboot automatically after update.
     */
    handleModelUpload = async (file: File, autoReboot: boolean) => {
        this.uploadPackage(file, 0x02, autoReboot);
    }

    /**
     * Uploads a firmware or model package to the device.
     * @param {File} file - The file to be uploaded.
     * @param {number} variant - The variant type indicating firmware or model.
     * @param {boolean} autoReboot - Indicates whether the device should reboot automatically after update.
     */
    uploadPackage = async (file: File, variant: number, autoReboot: boolean) => {
        if (this._dfuChannel !== null && !this._updateInProgress) {
            this._updateInProgress = true;

            // Define file reading function
            const fileReader = new FileReader();

            fileReader.onload = async () => {
                const arrayBuffer = fileReader.result as ArrayBuffer;
                const dataView = new DataView(arrayBuffer);

                const chunkSize = 512;  // Default MTU size
                const totalChunks = Math.ceil(dataView.byteLength / chunkSize);

                if (this._startCallback) {
                    this._startCallback({ fileSize: dataView.byteLength, chunkSize: chunkSize, totalChunks: totalChunks });
                }

                let remainingChunks = totalChunks;

                this._percentage = 0;

                try {

                    // Start firmware download mode
                    const startPackage = new Uint8Array([variant]);
                    await this._dfuChannel!.writeValue(startPackage);

                    for (let i = 0; i < totalChunks; i++) {
                        if (this._cancel) {
                            if (this._endCallback) {
                                this._endCallback({ rebootNeeded: !autoReboot });
                            }
                            this._cancel = false;
                            this._updateInProgress = false;
                            this._percentage = 0;
                            return;
                        } else {
                            const start = i * chunkSize;
                            const end = Math.min(start + chunkSize, dataView.byteLength);
                            const chunk = new Uint8Array(dataView.buffer.slice(start, end));
                            await this._sendFirmwareChunk(chunk, remainingChunks, totalChunks);
                            remainingChunks--;
                            this._percentage = ((totalChunks - remainingChunks) / totalChunks) * 100;
                        }
                    }

                    // Reboot device and apply update
                    if (autoReboot) {
                        const end = new Uint8Array([0xFF]);
                        try {
                            await this._dfuChannel!.writeValue(end);
                        } catch (e) {
                            console.warn(e);
                        }
                        this._updateInProgress = false;
                    }
                } catch (error) {
                    if (error instanceof Error) {
                        console.error("Device disconnected before update was finished! Update failed most likely. " + error.message);
                    } else {
                        console.error("Device disconnected before update was finished!");
                    }
                }
                if (this._endCallback) {
                    this._endCallback({ rebootNeeded: !autoReboot });
                }
            };

            try {
                fileReader.readAsArrayBuffer(file);
            } catch (e) {
                if (this._endCallback) {
                    this._endCallback({ rebootNeeded: false, error: e });
                }
                this._updateInProgress = false;
            }
        }
    }

    /**
     * Reboots the device after an update if necessary.
     */
    rebootAfterUpdate = async () => {
        if (this._dfuChannel !== null && this._updateInProgress) {
            const end = new Uint8Array([0xFF]);
            this._dfuChannel!.writeValue(end);
            this._updateInProgress = false;
        }
    }

    cancel = async () => {
        if (this._dfuChannel !== null && this._updateInProgress) {
            this._cancel = true;
        }
    }

    updateInProgress = () => {
        return this._updateInProgress;
    }

    /**
     * Sends a chunk of firmware data to the device.
     * @param {Uint8Array} chunk - The chunk of firmware data.
     * @param {number} remainingChunks - The number of chunks remaining to be sent.
     * @param {number} totalChunks - The total number of chunks to be sent.
     */
    _sendFirmwareChunk = async (chunk: Uint8Array, remainingChunks: number, totalChunks: number) => {
        const buffer = new Uint8Array(chunk);
        await this._dfuChannel!.writeValue(buffer);
        const progress = ((totalChunks - remainingChunks) / totalChunks) * 100;
        if (this._updateCallback) {
            this._updateCallback({ progress: progress, remainingChunks: remainingChunks, totalChunks: totalChunks });
        }
    };
}
